@import "../../../app/app-variables";
@import "../../../app/app";
@import "../../_categories";

aside.cat-aside-narrow,
aside.cat-aside-wide {
    display: block;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    overflow: hidden;

    div.category-icon {
        height: $gr-ll * 100%;
        padding-top: $gr-l * 1em;

        // svg.category-icon {
        //     transform: scale($gr-hh);
        // }
    }

    h2.category-name {
        align-items: center;
        display: flex;
        justify-content: center;
    }

    nav.cat-aside-narrow,
    nav.cat-aside-wide {
        background-color: turquoise;
        bottom: 0;
        height: $gr-ll * 100%;
        // position: absolute;
        right: 0;
        width: 100%;
    }

    div.cat-aside {
        align-content: space-evenly;
        color: $cl;
        display: flex;
        flex-wrap: wrap;
        height: $cat-aside-space-h;
        justify-content: center;
        position: relative;
        text-align: center;
    }    
}

aside.cat-aside-narrow {
    background-image: linear-gradient(to right, rgba(1, 0, 0, 0), rgba(0, 0, 0, 1));
    font-size: $gr-h * 100%;
    opacity: $gr-hh;
    width: $cat-narr-w;

    h2.category-name {
        height: ($gr-h + $gr-lll + $gr-lllh) * 100%;
        text-orientation: upright;
        width: 100%;
        writing-mode: vertical-lr;
    }

    div.cat-aside-narrow-spacer {
        display: none;
    }
}

aside.cat-aside-wide {
    // background-color: peru;

    // background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(rgba(255, 255, 255, .15)), to(rgba(0, 0, 0, .25))), -webkit-gradient(linear, left top, right bottom, color-stop(0, rgba(255, 255, 255, 0)), color-stop(0.5, rgba(255, 255, 255, .1)), color-stop(0.501, rgba(255, 255, 255, 0)), color-stop(1, rgba(255, 255, 255, 0)));

    // background-color: #013A6B;
    //   background-image: -webkit-linear-gradient(145deg, #013A6B 70%, #004E95 30%);
    background-image: -webkit-linear-gradient(135deg, $bg-cd 70%, rgba(7, 7, 7, 0.9) 30%);
    font-size: $gr-h * 100%;
    width: $cat-wide-w;

    h2.category-name {
        height: $gr-ll * 100%;
    }

    div.cat-aside-wide-spacer {
        // background-color: sienna;
        height: $cat-aside-space-h;
    }
}
