@import "../../app/app-variables";
@import "../../app/app";
@import "../_categories";

// skos
// background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(rgba(255, 255, 255, .15)), to(rgba(0, 0, 0, .25))), -webkit-gradient(linear, left top, right bottom, color-stop(0, rgba(255, 255, 255, 0)), color-stop(0.5, rgba(255, 255, 255, .1)), color-stop(0.501, rgba(255, 255, 255, 0)), color-stop(1, rgba(255, 255, 255, 0)));

div.category {
    height: 100%;
    overflow: auto;
}

div.category::-webkit-scrollbar {
    display: none;
}

div.category-aside-wide {
    margin-right: $cat-wide-w;
}

div.category-list {
    ul.drinks-list {
        li.drinks-list {
            span.drinks-list-pos {
                display: none;
            }
        }
    }
}

div.category-prefs {
    ul.drinks-list {
        div.drink {
            width: 100%;
        }

        li.drinks-list {
            span.drinks-list-pos {
                display: none;
            }
        }
    }
}

div.category-prefs::-webkit-scrollbar {
    display: none;
}

div.category-random {
    background-color: $bg-cd;
    color: $cl;

    div.drink {
        height: $gr-h * 100%;
        left: 0;
        position: relative;
        width: $gr-hh * 100%;
    }

    div.drinks-random {
        align-items: center;
        display: flex;
        height: $gr-l * 100%;
        justify-content: center;
        position: relative;
        width: $gr-hh * 100%;

        button.drinks-random {
            align-items: center;
            display: flex;
            height: $gr-lh * 100%;
            justify-content: space-between;
            position: relative;
            width: $gr-h * 100%;

            img.drinks-random {
                height: 100%;
                width: auto;
            }
        }
    }
}

div.category-top {
    ul.drinks-list {
        li.drinks-list {
            span.drinks-list-pos {
                background-color: $cat-top-c;
            }
        }
    }
}
