@import "../app/app-variables";
@import "../app/app";

ul.ings {
    align-content: stretch;
    align-items: stretch;
    background-color: $bg-cd;
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    justify-content: space-evenly;
    list-style: none;
    position: relative;

    li.ings {
        flex-basis: 33%;
        flex-grow: 2;
        max-width: $gr-h * 100%;
        position: relative;
        text-align: center;

        align-items: center;
        display: flex;
        justify-content: center;
    }

    li.ings:nth-child(odd) {
        background-color: $bg-cd;
        color: $cl;
    }

    li.ings:nth-child(even) {
        background-color: $bg-cl;
        color: $cd;
    }
}
