@import "../../app/app-variables";
@import "../..//app/app";

div.drink {
  align-items: stretch;
  align-content: flex-start;
  // background-color: wheat;
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;

  h2.drink-name {
    // background-color: mediumorchid;
    flex-shrink: 0;
    height: $gr-lh * 100%;
    position: relative;
    width: 100%;
  }

  div.drink-icon {
    // background-color: yellowgreen;
    display: block;
    height: $gr-hl * 100%;
    width: $gr-lh * 100%;
  }

  ul.drink-ings {
    // background-color: powderblue;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: $gr-hl * 100%;
    list-style-type: disc;
    position: relative;
    width: $gr-h * 100%;
  }

  li.drink-ings {
    margin-left: $gr-llh * 100%;
    padding: $gr-lll * 1em 0;
    position: relative;
  }

  li.drink-ings-sep {
    display: none;
  }

  nav.drink {
    // background-color: mediumaquamarine;
    display: block;
    height: $gr-hl * 100%;
    text-align: center;
    width: $gr-ll * 100%;
  }

  button.drink {
    padding: $gr-l * 1em;
    position: relative;
    top: $gr-hl * 100%;
    transform: translateY(-50%);
  }
}
