@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";

ul.ings {
    li.ings {
        span.ing-prefs {
            align-items: center;
            display: inline-flex;
            height: $gr-lh * 100%;
            justify-content: center;
            position: relative;
            width: $gr-hhh * 100%;
        }

        span.ing-prefs-wanted {
            background-color: #0ba9b3;
        }

        span.ing-prefs-wanted-not {
            background: none;
            background-color: #80707d;
            color: #ff93ee;
            transform: translateX(5.6%) rotate(8deg);
            text-decoration: line-through;
        }

        button.ing-prefs {
            background: transparent;
            height: $gr-l * 100%;
            position: absolute;
            width: $gr-ll * 100%;
        }

        button.ing-prefs-want {
            right: $gr-llh * 100%;
            top: $gr-lll * 100%;
        }

        button.ing-prefs-want-not {
            bottom: $gr-lll * 100%;
            left: $gr-llh * 100%;
        }

        img.ing-prefs {
            height: 100%;
            width: auto;
        }
    }
}
