@import "../../app/app-variables";
@import "../../app/app";

div.drink-custom-glass {
    overflow: hidden;
    position: relative;
    width: 100%;

    div.drink-custom-glass-level {
        align-content: space-evenly;
        background-color: #ff85a4; // orchid;
        bottom: 0;
        display: block;
        height: 10%;
        left: 50%;
        padding-bottom: 10%;
        position: absolute;
        transform: translateX(-50%);
        width: 60%;
    }

    svg.drink-custom-glass {
        top: 0;
        display: inline-block;
        height: 100%;
        left: 50%;
        position: absolute;
        transform: translateX(-50%);
    }

    svg.drink-custom-glass1 {
        fill: #fff452; //#ffed00;
    }

    svg.drink-custom-glass2 {
        fill: none;
        stroke: #1b3740; //#53002e;
        stroke-miterlimit: "10";
        stroke-width: "2.65";
    }

    svg.drink-custom-glass3 {
        fill: none;
        stroke: #7d8499; // #53002e;
        stroke-miterlimit: "10";
        stroke-width: "1.325";
    }

    svg.drink-custom-glass-bg {
        // fill: teal;
    }

    ul.drink-custom-glass-level {
        align-content: space-evenly;
        bottom: 0;
        display: flex;
        flex-wrap: wrap;
        height: 10%;
        left: 50%;
        list-style: none;
        padding-bottom: 10%;
        position: absolute;
        transform: translateX(-50%);
        width: 60%;

        li.drink-custom-glass-level {
            align-items: center;
            color: $cl;
            display: flex;
            font-size: $grs-l * 100%;
            // font-size: $gr-l * 100%;
            justify-content: center;
            width: 100%;
        }
    }
}
