@import "../../app/app-variables";
@import "../../app/app";

$sepWidth: ($gr-llh * 1em);

div.drinks-list-item {
  // background-color: tan;
  display: block;
  height: 100%;
  position: relative;

  // drink

  h2.drink-name {
    display: block;
    height: $gr-h * 100%;
    // height: $gr-l * 100%;
    position: relative;
  }

  // ings

  ul.drink-ings {
    // background: teal;
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: $gr-l * 100%;
    // height: $gr-h * 100%;
    justify-content: space-evenly;
    list-style-type: none;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    left: $sepWidth;
    // width: $gr-hl * 100%;
    width: 100%;
    z-index: 1;
  }

  li.drink-ings {
    flex-grow: 5;
    left: $sepWidth;
    min-width: 21%;
    // outline: 1px solid turquoise;
    padding: 0 1em;
    position: relative;
    text-align: center;
  }

  li.drink-ings-sep {
    // background-color: tomato;
    flex-basis: auto;
    flex-grow: 0;
    // left: 3%;
    min-width: $sepWidth;
    padding: 0;
    // padding: 0;
    width: $sepWidth;
  }
}

li.drinks-list:nth-child(odd) {
  div.drinks-list-item {
    li.drink-ings-sep {
      border-right: ($gr-llh * 1em) solid $bg-cl;
    }
  }
}

li.drinks-list:nth-child(even) {
  div.drinks-list-item {
    li.drink-ings-sep {
      border-right: ($gr-llh * 1em) solid $bg-cd;
    }
  }
}
