@import "../../app/app-variables";
@import "../../app/app";

ul.drinks-list::-webkit-scrollbar {
  scroll-behavior: smooth;
}

ul.drinks-list {
  display: block;
  scroll-behavior: smooth;

  li.drinks-list-empty {
    align-items: center;
    display: flex;
    height: 100%;
    padding: 0 $gr-l * 100%;
    justify-content: center;
  }

  li.drinks-list {
    cursor: pointer;
    display: block;
    height: ($gr-lh + $gr-lll) * 100vh;
    position: relative;

    span.drinks-list-pos {
      align-items: center;
      border-radius: 50%;
      color: $cd;
      display: flex;
      height: 2em;
      justify-content: center;
      left: 1em;
      position: absolute;
      top: 1em;
      width: 2em;
    }
  }

  li.drinks-list-selected {
    height: ($gr-l + $gr-llh) * 100vh;
  }

  div.drink {
    height: 100%;
    margin: 0 auto;
    position: relative;
    width: $gr-hl * 100%;
  }

  li.drinks-list:nth-child(odd) {
    background-color: $bg-cd;
    color: $cl;
  }

  li.drinks-list:nth-child(even) {
    background-color: $bg-cl;
    color: $cd;
  }
}
