@import "../app/app-variables";
@import "../app/app";

div.keyboard {
    display: flex;
    flex-wrap: wrap;
    height: 38%;
    overflow: hidden;
    position: relative;

    div.keyboard-row {
        align-items: center;
        display: flex;
        flex-wrap: no-wrap;
        height: 16.5%;
        justify-content: space-around;
        position: relative;
        width: 100%;

        span.keyboard-btn {
            align-items: center;
            border-radius: $gr-ll * 1em;
            display: flex;
            height: $gr-hhh * 100%;
            justify-content: center;
            min-width: $gr-llh * 100%;
            vertical-align: middle;
        }
    }

    div.keyboard-row-txt {
        span.keyboard-txt {
            align-items: center;
            border-radius: $gr-ll * 1em;
            display: flex;
            height: $gr-hh * 100%;
            justify-content: center;
            overflow: hidden;
            width: $gr-h * 100%;
            white-space: nowrap;
        }

        span.keyboard-txt-placeholder {
            color: gray;
        }

        span.keyboard-btn {
            background-color: mediumorchid;
            overflow: hidden;
            padding: 0 $gr-h * 1em;
        }
    }

    div.keyboard-r4 {
        span.keyboard-btn {
            width: $gr-h * 100%;
        }
    }

    .keyboard-btn {
        box-sizing: border-box;
        text-align: center;
        cursor: pointer;
        border-color: silver; // #f2f2f2;
        border-style: solid;
        text-shadow: 0 0.5px 1px #777, 0 2px 6px #f2f2f2;
        border-width: 1px;
        background: -webkit-linear-gradient(top, #f9f9f9 0%, #d2d2d2 80%, #c0c0c0 100%);
        font-family: sans-serif;
        // transition: box-shadow 0.1s ease, transform 0.05s ease;
        // box-shadow: 0 0 1px #888, 0 1px 0 #fff, 0 6px 0 #c0c0c0, 0 8px 17px rgba(#444, 0.4),
        //     2px 1px 4px rgba(#444, 0.25), -2px 1px 4px rgba(#444, 0.25), 0 9px 16px rgba(#444, 0.1);

        // &:hover,
        // &:focus {
        // box-shadow: 0 0 1px #888, 0 1px 0 #fff, 0 4px 0 #c0c0c0, 0 2px 35px rgba(#444, 0.3),
        //     2px 2px 4px rgba(#444, 0.25), -2px 2px 4px rgba(#444, 0.25), 0 7px 4px rgba(#444, 0.1);
        //     transform: translateY(2px);
        // }

        &:active {
            box-shadow: 0 0 1px #888, 0 1px 0 #fff, 0 0 0 #c0c0c0, 0 0px 30px rgba(#444, 0.15),
                2px 2px 4px rgba(#444, 0.25), -2px 2px 4px rgba(#444, 0.25), 0 0px 4px rgba(#444, 0.25);
            transform: translateY(4px);
        }
    }
}
