@import "../../app/app-variables";
@import "../../app/app";
@import "../../categories/_categories";

ul.ings {
    li.ings {
        div.ing-custom {
            align-items: flex-end;
            // background-color: palegoldenrod;
            display: flex;
            flex-wrap: wrap;
            height: $gr-h * 100%;
            justify-content: space-evenly;
            // text-align: center;
            width: 100%;

            span.ing-custom-pos {
                background-color: #fff452;
                border-radius: $gr-lh * 100%;
                color: $cd;
                left: $gr-ll * 1em;
                position: absolute;
                top: $gr-ll * 1em;
            }

            p.ing-custom {
                align-self: flex-start;
                // background-color: hotpink;
                width: 100%;
            }

            span.ing-custom {
                // background-color: cornflowerblue;
                border-radius: $gr-lh * 100%;
                border: 3px solid #7d8499; // gray;
                height: 1em;
                width: 1em;
            }

            span.ing-custom-selected {
                background-color: #ff85a4; // orchid;
            }

            span.ing-custom-hidden {
                visibility: hidden;
            }
        }
    }
}
